<template>
  <div class="info-panel">
    <v-card>
      <v-card-title primary-title>{{ gameConfig.title }} </v-card-title>
      <v-card-text>
        <ul>
          <li>{{ gameConfig.teamNumber }}</li>
          <li>{{ JSON.stringify(gameConfig.teammembers) }}</li>
          <li>{{ gameConfig.days }}</li>
          <li>{{ gameConfig.dayLong }}</li>
          <li>{{ gameConfig.defaultBalance }}</li>
          <li>{{ gameConfig.cost.wage }}</li>
        </ul>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import * as readable from '../lib/readable'
import * as constant from '../lib/constant'

export default {
  props: {
    'game-config': Object
  }
}
</script>

<style>
</style>



