<template>
  <div>
    <v-toolbar dark class="cyan">
      <v-toolbar-title>{{ title }}</v-toolbar-title>
    </v-toolbar>
    <main>
      <v-card>
        <v-card-title primary-title>Create a Game</v-card-title>
        <v-card-text>
          <v-text-field
            v-model="configText"
            label="Config"
            multi-line
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat class="orange--text" @click.native="newGame">CREATE</v-btn>
        </v-card-actions>
      </v-card>
    </main>
    <v-snackbar
      :timeout="6000"
      secondary
      v-model="snackbar"
    >
      {{ snackbarText }}
      <v-btn dark flat @click.native="snackbar = false"></v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import {router} from '../../../router'
import * as constant from '../../../lib/constant'
import * as readable from '../../../lib/readable'
import * as api from '../../../lib/api'
import * as enterApi from '../../../lib/api/enter'

export default {
  data () {
    return {
      title: 'ADMIN',
      snackbar: false,
      snackbarText: '',
      configText: ''
    }
  },
  methods: {
    newGame () {
      enterApi.newGame(JSON.parse(this.configText))
        .then((function (res) {
          let data = res.data
          api.nowUser.setGameId(data.gameId)
          api.nowUser.setGameConfig(data.gameConfig)
          api.nowUser.setTeam(constant.TEAMS.STAFF)
          api.nowUser.setJob(constant.STAFF_JOBS.CONSOLER)
          router.push('/round/consoler')
        }).bind(this))
        .catch(function (err) {
          console.error(err)
        })
    },
    announce (msg) {
      this.snackbarText = msg
      this.snackbar = true
    }
  },
  mounted () {
    this.configText = JSON.stringify({
      title: ' REDRO',
      describe: '2017  ',
      teamNumber: 4,
      teammembers: [12, 12, 13, 14],
      days: 3,
      dayLong: 300,
      defaultBalance: 19400,
      cost: {
        storage: {
          [constant.JOBS.FACTORY]: {
            [constant.PRODUCTS.CAR]: 3000,
            [constant.PRODUCTS.WHEEL]: 200,
            [constant.PRODUCTS.BODY]: 200,
            [constant.PRODUCTS.ENGINE]: 200
          },
          [constant.JOBS.WHOLESALER]: {
            [constant.PRODUCTS.CAR]: 6000
          },
          [constant.JOBS.RETAILER]: {
            [constant.PRODUCTS.CAR]: 9000
          },
          patchSize: 10,
          permanent: true
        },
        transport: {
          cost: 200,
          patchSize: 4
        },
        wage: 100
      },
      updateInterval: 1000,
      news: [
        {
          day: 1,
          time: 0,
          title: '',
          content: '',
          picture: 'image/news/day1_rehearsal.jpg',
          demanded: 20,
          price: 3500
        },
        {
          day: 2,
          time: 0,
          title: '',
          content: '  10%  15% 50%',
          picture: 'image/news/day2_rehearsal.jpg',
          demanded: 30,
          price: 3500
        },
        {
          day: 3,
          time: 0,
          title: '',
          content: '311ToyotaMitsubishiNissan',
          picture: 'image/news/day3_rehearsal.jpg',
          demanded: 20,
          price: 3500
        }
      ]
    }, null, '  ')
  }
}
</script>

<style>
</style>



