<template>
  <v-dialog
    class="inline-dialog"
    v-model="accountDialog"
    persistent
  >
    <v-btn
      class="activator"
      slot="activator"
      primary
    >
      
    </v-btn>
    <v-card>
      <v-card-title></v-card-title>
      <v-card-text>
        <v-select
          v-bind:items="teamList"
          v-model="selectedTeam"
          label=""
          single-line
          item-value="index"
          bottom
        ></v-select>
        <v-select
          v-bind:items="actionList"
          v-model="selectedAction"
          label=""
          single-line
          item-value="index"
          bottom
        ></v-select>
        <v-text-field
          v-model="balance"
          label=""
          type="number"
          suffix=""
          min="0"
        ></v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn :class="color.accent" flat="flat" @click.native="accountDialog = false"></v-btn>
        <v-btn :class="color.accent" flat="flat" @click.native="act"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import * as readable from '../lib/readable'
import * as constant from '../lib/constant'
import * as api from '../lib/api'
import * as accountApi from '../lib/api/account'

export default {
  props: {
    'announce': Function
  },
  data: function () {
    return {
      accountDialog: false,
      selectedAction: null,
      selectedTeam: null,
      amount: null,
      balance: null,
      state: api.nowUser.getState(),
      color: api.nowUser.getColor()
    }
  },
  computed: {
    teamList () {
      return readable.toReadableTeamList(api.nowUser.getTeamNumber())
    },
    actionList () {
      return [{
        index: 'TAKE',
        text: ''
      }, {
        index: 'GIVE',
        text: ''
      }]
    }
  },
  methods: {
    act () {
      this.accountDialog = false
      switch (this.selectedAction) {
        case 'TAKE':
          accountApi.take(api.nowUser.getGameId(), this.selectedTeam, this.balance)
            .then((function (res) {
              let data = res.data
              this.announce(`${data.teamIndex}${data.balance}`)
            }).bind(this))
            .catch((function (err) {
              console.log(err)
              this.announce(err.data && (err.data.readableMsg || err.data.msg))
            }).bind(this))
          break
        case 'GIVE':
          accountApi.give(api.nowUser.getGameId(), this.selectedTeam, this.balance)
            .then((function (res) {
              let data = res.data
              this.announce(`${data.teamIndex}${data.balance}`)
            }).bind(this))
            .catch((function (err) {
              console.log(err)
              this.announce(err.data && (err.data.readableMsg || err.data.msg))
            }).bind(this))
          break
      }
    }
  }
}
</script>

<style>
</style>



